import { GrpcUnaryError, GrpcUnaryRequest, GrpcUnaryResponse } from 'src/shared/GrpcUnary';

type SendNetworkMessageArgs = {
	partialNetworkMessage: PartialNetworkMessage;
	networkId: string;
};

export const sendNetworkMessage = ({
	partialNetworkMessage,
	networkId
}: SendNetworkMessageArgs) => {
	window.postMessage({
		source: 'grpc-web-devtools',
		partialNetworkMessage,
		networkId
	});
};

type PartialNetworkMessage = UnaryRequestMessage | UnaryResponseMessage | UnaryErrorMessage;

export type UnaryRequestMessage = {
	type: 'unary-request';
	partial: GrpcUnaryRequest;
};

export type UnaryResponseMessage = {
	type: 'unary-response';
	partial: GrpcUnaryResponse;
};

export type UnaryErrorMessage = {
	type: 'unary-error';
	partial: GrpcUnaryError;
};
